import React, { useState } from 'react';
import { Row, Image, Button, Col, Container, Card, CardDeck, Form, InputGroup } from 'react-bootstrap'
import styles from '../styles.module.css';
import logoX from '../logoX.png'

const Login = () => {
  const [ formData, setFormData ] = useState({
    username: "", //Admin | Customer | Technician
    password: ""
  });
  const { username, password } = formData;
  const handleSubmit = () => {
    if(username === "admin"){
      localStorage.setItem("type", "admin");
      window.location.href = '/fungibility/admin-heatmap'
    }
    else if (username === "technician"){
      localStorage.setItem("type", "technician");
      window.location.href ='/fungibility/technician-heatmap'
    }
    else if(username === "customer") {
      localStorage.setItem("type", "customer");
      window.location.href = '/fungibility/repair-request'
    }
    else{
      localStorage.removeItem("type");
      alert("Incorrect username / password")
    }
  }
  return (<>
    <Row style={{marginTop: "40px"}}>
      <Col md={{ offset: 3, span: 6}}>
        <img src={logoX} alt="reDirectlogo_devops" className="w-100 h-auto" />
        <div className="w-100 h-auto">
          <h3>Login</h3>
          <div>
            <input 
              type="text" 
              placeholder="Username" 
              value={username}
              onChange={ (e) => 
                setFormData({ 
                  ...formData, 
                  username : e.target.value 
                })
              } 
            />
          </div>
          <br></br>
          <div>
            <input 
              type="password" 
              placeholder="Password" 
              value="fungibility"
              onChange={ (e) => 
                setFormData({ 
                  ...formData, 
                  password : e.target.value 
                })
              } 
            />
          </div>
          <br></br>
          <button onClick={handleSubmit}>Submit</button>
        </div>
        
        
      </Col>
    </Row>
  </>);
}

export default Login;