import React, { useState, useEffect } from "react";
import { slide as Menu } from "react-burger-menu";
import { Link } from 'react-router-dom'
import { Row } from 'react-bootstrap'
import logoX from "../../logoX.png"

export default props => {
  const [menuState, setMenuState] = useState(false)
  const [menuType, setMenuType] = useState('')
  useEffect(() => {
    const accountType = localStorage.getItem("type");
    setMenuType(accountType);
  }, [])

  const handleStateChange = (state) => {
    setMenuState(state.isOpen)
  }
  return (
    // Pass on our props

    <Row className="w-100">
      <Menu {...props} isOpen={menuState} onStateChange={(state) => handleStateChange(state)}>
        <Link className="menu-item p-1 pl-5 h6 font-weight-bold text-dark" onClick={() => setMenuState(false)} to="/">
          <img alt="" className="m-1 mt-3 mx-auto" style={{ position: "relative", height: "26px", width: "auto" }} src={logoX} />
          {/* <div style={{marginTop: "1em"}}>Fungibility</div> */}
        </Link>
        <hr />

        <div className="p-3 mb-0 pl-5" style={{ backgroundColor: "#3C1518", color: "white" }}>
          <h6 className="font-weight-bold"><u>Navigation</u></h6>
          {menuType === 'admin' && (<>
            <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="/admin-heatmap">Store Schedule</Link><br />
            <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="/config-schedule">Configure Schedule</Link><br />
          </>)}
          {menuType === 'technician' && (<>
            <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="/my-jobs">My Jobs</Link><br />
            <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="/technician-heatmap">Tech Schedule</Link><br />
            <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="/appointment-request">Appointment Request</Link><br />
          </>)}
          {menuType === 'customer' && (<>
            <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="/repair-request">Repair Request</Link><br />
          </>)}
          {/* <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="technician-heatmap">Option 3</Link><br />
          <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="template-page">Template Page</Link><br /> */}
          <Link className="pl-3 menu-item no-decoration" onClick={() => setMenuState(false)} to="/login">Logout</Link><br />
        </div>


      </Menu>
      <div>
        {/* <h2 style={{marginLeft: "3em", marginTop: "1em"}}>Fungibility</h2> */}
        <img alt="" style={{ position: "relative", left: "70px", top: "30px", height: "40px", width: "auto" }} src={logoX} />
        {/* <a className="btn btn-primary" label="Send Feedback" href="https://forms.office.com/Pages/ResponsePage.aspx?id=Mq3lZATL30SIlr7V13kkKTBtLhGpY4VKs6PycVQTpfxUQTZUUENBMlJMWU9FR0dLU0k1ME44TjVTRS4u"  style={{borderColor : "#000001", backgroundColor: "#000001", color: "#ffffff", position: "absolute", right: "100px", top: "30px", width: "auto"}}>
            <i class="fas fa-comments"></i>
        </a>
        <a className="btn btn-primary" label="Request Quick Link" href="https://forms.office.com/Pages/ResponsePage.aspx?id=Mq3lZATL30SIlr7V13kkKTBtLhGpY4VKs6PycVQTpfxUQzg5TFY3WENJRlA4SzZTV1VIMzhMOFFPNS4u" style={{borderColor : "#000001", backgroundColor: "#000001", color: "#ffffff", position: "absolute", right: "40px", top: "30px", width: "auto"}} label="Add Quick Link">
            <i class="fas fa-plus-circle fa_custom" ></i>
        </a> */}
      </div>

    </Row>
  );
};