import React, {useState, Component} from 'react'
import { Row, Image, Button, Col, Container, Card, CardDeck, Carousel, Form, Modal, Dropdown, InputGroup, FormControl, DropdownButton } from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom';
import Moment from 'react-moment';
import moment from 'moment';
import {Calendar,
  DateLocalizer,
  momentLocalizer,
  globalizeLocalizer,
  move,
  Views,
  Navigate,
  components} from 'react-big-calendar';
import Sidebar from './layout/Sidebar';
import 'react-big-calendar/lib/css/react-big-calendar.css';

const MyCalendar = (props) => {
    
    const localizer = momentLocalizer(moment);
    

    return (
        <>
            <Sidebar pageWrapId={"page-wrap"} outerContainerId={"page-wrap"} />
            <Container fluid className=""  style={{marginTop: "40px"}}>
                <Row className="mx-2 mb-5">
                    <Col md={8}>
                        <h3><b>{props.header} {props.subHeader !== "" ? (" - " + props.subHeader) : ""} </b> </h3>
                    </Col>
                    <Col md={{ span: 4}}>
                        {/* <Form>    
                            <Form.Group>
                                <Form.Row>
                                    <Form.Label column md={2}>Search</Form.Label>
                                    <Col md={10}>
                                        <InputGroup>
                                            <Form.Control type="text" />
                                            <InputGroup.Append>
                                                <Button variant="outline-secondary">
                                                    <i className="fa fa-search"></i>
                                                </Button>
                                            </InputGroup.Append>
                                        </InputGroup>
                                    </Col>
                                </Form.Row>
                            </Form.Group>
                        </Form> */}
                    </Col>
                </Row>
                <Row className="mx-2 ">
                    <div className="calendar-container" style={{
                        height: "100%",
                        width: "100%"
                        // ,
                        // marginTop: "5px"
                    }}>
                        <Calendar
                            localizer={localizer}
                            // events={events}
                            events={props.events}
                            style={{
                                height: "80vh",
                                width: "100%"
                            }}
                        />
                    </div>
                </Row>
                
                
            </Container>

        </>
    )
}

export default MyCalendar
