import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import './App.css';
import {
  CardDeck,
  Card,
  Image,
  Row,
  Col,
  SampleImage,
  SampleIcon,
  InputGroup,
  Button,
  Form,
  DropdownButton,
  Dropdown,
  Container
} from 'react-bootstrap';

import corecalendar from './corecalendar';
import MyCalendar from './components/Calendar';
import TechnicianHeatmap from './components/TechnicianHeatmap';
import AdminHeatmap from './components/AdminHeatmap';
import ConfigSchedule from './components/ConfigSchedule'

import AppointmentRequest from './components/AppointmentRequest'
import Login from './components/Login';
import CustomerHeatmap from './components/CustomerHeatmap';

const App = () => {
  // const contents = fetch('contents.json');
  return (

    <div id="page-wrap">

      <BrowserRouter basename="/fungibility">
        <Switch>
          <Route
            exact path="/repair-request"
            render={
              (props) => <CustomerHeatmap {...props}
              />
            }
          >
          </Route>
          <Route
            exact path="/admin-heatmap"
            render={
              (props) => <AdminHeatmap {...props}
              />
            }
          >
          </Route>
          <Route
            exact path="/config-schedule"
            render={
              (props) => <ConfigSchedule {...props}
              />
            }
          >
          </Route>
          <Route
            exact path="/technician-heatmap"
            render={
              (props) => <TechnicianHeatmap {...props}
              />
            }
          >
          </Route>
          <Route
            exact path="/my-jobs"
            render={
              (props) => <MyCalendar {...props}
                events={corecalendar}
                header="My Jobs"
              />
            }
          >
          </Route>
          <Route
            exact path="/appointment-request"
            render={
              (props) => <AppointmentRequest {...props}
              />
            }
          >
          </Route>
          <Route
            exact path="/login"
            render={
              (props) => <Login {...props}
              />
            }
          ></Route>
          <Route
            exact path="/"
            render={
              (props) => <Login {...props}
              />
            }
          ></Route>
        </Switch>

      </BrowserRouter>
    </div>
  );
}

export default App;
